/**
 * ThemeUtils.js - Utilitaires centralisÃ©s pour la gestion des thÃ¨mes
 * 
 * â VERSION SIMPLIFIÃE:
 * - Applique seulement les variables de base via setProperty()
 * - Les variables dÃ©rivÃ©es (color-mix) sont gÃ©rÃ©es par le CSS natif
 * - Pas de gÃ©nÃ©ration de CSS complexe
 */

// ============================================================================
// CONSTANTES
// ============================================================================

export const CSS_VAR_MAPPINGS = {
  'primaryColor': '--fp-primary-color',
  'backgroundColor': '--fp-background-color',
  'secondaryBackgroundColor': '--fp-secondary-background-color',
  'textColor': '--fp-text-color',
  'borderColor': '--fp-border-color',
  'fontFamily': '--fp-font-family',
  'baseRadius': '--fp-base-radius',
  'spacing': '--fp-spacing'
};

// ============================================================================
// UTILITAIRES DE CONVERSION
// ============================================================================

/**
 * Convertit une clÃ© de thÃ¨me en variable CSS prÃ©fixÃ©e
 * @param {string} key - ClÃ© du thÃ¨me (ex: 'primaryColor')
 * @returns {string} Variable CSS (ex: '--fp-primary-color')
 */
export function convertToCssVar(key) {
  return CSS_VAR_MAPPINGS[key] || `--fp-${key.replace(/([A-Z])/g, '-$1').toLowerCase()}`;
}

/**
 * Nettoie et valide un objet thÃ¨me
 * @param {Object} theme - Objet thÃ¨me Ã  nettoyer
 * @returns {Object} ThÃ¨me nettoyÃ©
 */
export function sanitizeTheme(theme) {
  if (!theme || typeof theme !== 'object') return {};
  
  const sanitized = {};
  
  Object.entries(theme).forEach(([key, value]) => {
    if (value !== null && value !== undefined && value !== '') {
      sanitized[key] = String(value).trim();
    }
  });
  
  return sanitized;
}

/**
 * Valide qu'un objet thÃ¨me est valide
 * @param {Object} theme - Objet thÃ¨me Ã  valider
 * @returns {boolean} true si valide
 */
export function isValidTheme(theme) {
  if (!theme || typeof theme !== 'object') return false;
  
  // Au moins une propriÃ©tÃ© valide
  return Object.keys(theme).some(key => 
    theme[key] !== null && 
    theme[key] !== undefined && 
    theme[key] !== ''
  );
}

// ============================================================================
// APPLICATION DU THÃME - VERSION SIMPLIFIÃE
// ============================================================================

/**
 * Applique un thÃ¨me sur un Ã©lÃ©ment DOM
 * â VERSION SIMPLIFIÃE: Applique seulement les variables de base
 * Les variables dÃ©rivÃ©es (color-mix) sont automatiquement recalculÃ©es par CSS
 * 
 * @param {Object} theme - Objet thÃ¨me
 * @param {HTMLElement} element - ÃlÃ©ment DOM cible
 * @param {Object} options - Options { setDataTheme: boolean }
 * @returns {boolean} true si succÃ¨s
 */
export function applyThemeToElement(theme, element, options = {}) {
  if (!theme || !element) return false;
  
  const { setDataTheme = true } = options;
  
  try {
    // â Nettoyer le thÃ¨me
    const sanitizedTheme = sanitizeTheme(theme);
    
    // â Appliquer seulement les variables de base
    Object.entries(sanitizedTheme).forEach(([key, value]) => {
      if (key !== 'base') {
        const cssVar = convertToCssVar(key);
        element.style.setProperty(cssVar, value);
      }
    });
    
    // â Appliquer l'attribut data-theme pour les surcharges CSS
    if (setDataTheme && sanitizedTheme.base) {
      element.setAttribute('data-theme', sanitizedTheme.base);
    }
    
    return true;
    
  } catch (error) {
    console.error('Failed to apply theme to element:', error);
    return false;
  }
}

// ============================================================================
// EXPORTS GROUPÃS
// ============================================================================

export default {
  convertToCssVar,
  sanitizeTheme,
  isValidTheme,
  applyThemeToElement,
  CSS_VAR_MAPPINGS
};